@import url(https://fonts.googleapis.com/css2?family=Bona+Nova:ital,wght@0,400;0,700;1,400&display=swap);
body {
  margin: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  font-family: "Bona Nova", serif;
}

html {
  font-size: 62.5%;
}

.app {
  width: 100vw;
  height: auto;
}
.Maincontainer {
  width: 80%;
  margin: 0 auto;
  margin-top: 1rem;
  height: auto;
  /* background-color: #dfe4ea; */
}

.pagesButton {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0rem 2rem;
}

.footer {
  height: 20vh;
  width: 100%;
  background-color: #000;
  color: #fff;
  margin-top: 3rem;
  position: absolute;
}

.footerIcons {
  width: 20%;
  height: 50%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;

  /* background-color: #fff; */
}
.footerIcons > a > .MuiSvgIcon-root {
  font-size: 3.5rem;
  text-decoration: none;
  color: #fff;

  /* background-color: #fff; */
}

.footerInc {
  font-size: 1.3rem;
  text-align: center;
  position: relative;
  bottom: -5rem;
}

.header {
  width: 100%;
  height: 7rem;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 30rem 1fr 20rem;
  grid-template-rows: 7rem;
  padding-right: 3rem;
}

.header__left {
  /* object-fit: contain; */
  display: grid;
  place-items: center;
  padding-right: 3rem;
}
.header__left > .MuiSvgIcon-root {
  color: #000;
  font-size: 3rem;
}

.logoImg {
  height: 6rem;
  object-fit: contain;
  width: 100%;
}
.header__mid {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.searchBar {
  background-color: #ced6e0;
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
  margin-right: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 1rem;
  /* border-top-right-radius: 0rem;
  border-bottom-left-radius: 0rem; */
  padding: 0.5rem 0.6rem;
}

form {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.searchBar > form > .MuiSvgIcon-root {
  margin-left: 0.7rem;
  font-size: 2.5rem;
  color: rgb(60, 65, 70);
  cursor: pointer;
}
#search {
  width: 100%;
  padding: 0.6rem 1rem;
  border: none;
  outline: none;
  border-radius: 1rem;
  border-top-right-radius: 0rem;
  border-bottom-left-radius: 0rem;
}

/* a {
  text-decoration: none;
  font-size: 1.7rem;
  font-weight: 500;
  color: #000;
} */
.header__mid_link {
  -webkit-flex: 0.2 1;
          flex: 0.2 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.header__mid_link > .MuiButton-root > .MuiButton-label {
  font-weight: bold;
  font-size: 1.5rem;
}
.headerMid__list {
  list-style-type: none;
  padding: 0.6rem 0.4rem;
  transition: all 0.2s ease;
  color: #000;
  font-weight: bold;
  cursor: pointer;
  -webkit-backdrop-filter: blur(20rem);
          backdrop-filter: blur(20rem);
}
.headerMid__list:hover {
  background-color: rgba(0, 0, 0, 0.733);
  -webkit-backdrop-filter: blur(20rem);
          backdrop-filter: blur(20rem);
  border-radius: 1.5rem;
  padding: 0.6rem 0.4rem;
  color: #ffffff;
}
.listLink {
  font-weight: bold;
}
.listLink:hover {
  color: #ffffff;
}
.header__right {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  /* background-color: #000; */
}

.header__right > .MuiButton-root > .MuiButton-label {
  font-size: 1.1rem;
  font-weight: bold;
}

.row {
  width: 100%;
  height: 25rem;
  margin-bottom: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.rowLeft {
  height: 25rem;
  width: 20%;
  display: grid;
  place-items: center;
}
.rowRight {
  background-color: #dfe4ea;
  width: 80%;
  height: 20rem;
  /* display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around; */
}
.rowRight::-webkit-scrollbar {
  display: none;
}

.rowLeft > #posterImg {
  height: 100%;
}

.title {
  margin-left: 1rem;
  font-size: 2.6rem;
  font-weight: bold;
  color: #1e272e;
  margin-bottom: 0.7rem;
}

.rates {
  margin-left: 1rem;
  font-size: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
#Grade {
  color: rgb(255, 196, 0);
  font-size: 2rem;
}
.desc {
  margin-left: 1rem;
  margin-top: 3rem;
  margin-right: 5rem;
  font-size: 1.6rem;
  margin-bottom: 2rem;
}
.votes {
  margin-left: 1rem;
  font-size: 2rem;
}
.date {
  margin-left: 1rem;
  font-size: 1.5rem;
}

